import React, { useEffect, useState } from 'react';
import './App.css';
import { Button, Col, Row } from 'antd';
import * as VKID from '@vkid/sdk';

function App() {
  interface User {
    firstName: string;
    // Add other user properties as needed
  }

  const [user, setUser] = useState<User | null>(null);

  useEffect(() => {
    VKID.Config.init({
      app: 53306336,
      redirectUrl: 'https://stanislawpivovarov.github.io/vk_video_shuffler/',
    });

  }, []);

  const auth = async () => {
    try {
      const result: any = await VKID.Auth.login();
      if (result) {
        setUser(result);
      }
    } catch (error) {
      console.error('Ошибка авторизации:', error);
    }
  };

  return (
    <div className="App">
      <Row justify={'center'}>
        <Col span={10}>
          {user ? (
            <div>
              <h2>Добро пожаловать, {user.firstName}!</h2>
            </div>
          ) : (
            <Button type="primary" onClick={auth}>
              Войти через VK ID
            </Button>
          )}
        </Col>
      </Row>
    </div>
  );
}

export default App;